import React, { useContext, useRef, useState } from "react";
import "./Share.css";
import { Analytics, Face, Gif, Image } from "@mui/icons-material";
import { AuthContext } from "../../../State/AuthContext";
import axios from "axios";

const Share = () => {
  const { user } = useContext(AuthContext);
  const [file, setFile] = useState(null);

  const PUBLIC_FOLDER = process.env.REACT_APP_PUBLIC_FOLDER;

  const inputDOM = useRef(null);

  const handleSubmit = async (e) => {
    e.preventDefault();
    if (!inputDOM.current.value) return;
    const newPost = { userId: user._id, description: inputDOM.current.value };

    if (file) {
      // 
      const fileData = new FormData();
      // file.nameDatenow()
      const fileName = Date.now() + file.name;
      //  append() 
      fileData.append("name", fileName);
      fileData.append("file", file);
      // fileData = { name: fileName, file: file } 

      newPost.img = fileName;

      try {
        // backendpublic/img
        await axios.post("/upload", fileData);
      } catch (err) {
        console.log(err);
      }
    }

    try {
      // 
      await axios.post("/posts", newPost);
      window.location.reload();
    } catch (err) {
      console.log(err);
    }
  };

  return (
    <div className="share">
      <div className="shareWrapper">
        <div className="shareTop">
          <img src={user.profilePicture ? PUBLIC_FOLDER + user.profilePicture : `${PUBLIC_FOLDER}/noAvatar.png`} alt="" className="shareProfileImg" />
          <input type="text" className="shareInput" placeholder="" ref={inputDOM} />
        </div>
        <hr className="shareHr" />
        <form
          className="shareButtons"
          onSubmit={(e) => {
            handleSubmit(e);
          }}>
          <div className="shareOptions">
            {/* label inputbutton, textarea, ...jsx htmlFor="id" HTML for="id" */}
            <label className="shareOption" htmlFor="file">
              <Image className="shareIcon" htmlColor="blue" />
              <span className="shareOptionText"></span>
              <input
                id="file"
                type="file" // file 
                accept=".png, .jpeg, .jpg" // 
                style={{ display: "none" }} // 
                onChange={(e) => {
                  setFile(e.target.files[0]);
                }} // onChange
              />
            </label>
            <div className="shareOption">
              <Gif className="shareIcon" htmlColor="hotpink" />
              <span className="shareOptionText">GIF</span>
            </div>
            <div className="shareOption">
              <Face className="shareIcon" htmlColor="green" />
              <span className="shareOptionText"></span>
            </div>
            <div className="shareOption">
              <Analytics className="shareIcon" htmlColor="red" />
              <span className="shareOptionText"></span>
            </div>
          </div>
          <button className="shareButton" type="submit">
            
          </button>
        </form>
      </div>
    </div>
  );
};

export default Share;
